@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
.dark-theme {
  --BG: hsl(230, 17%, 14%);
  --BGPattern: hsl(232, 19%, 15%);
  --CardBG: hsl(228, 28%, 20%);
  --DText: hsl(228, 34%, 66%);
  --Text: hsl(0, 0%, 100%);
  --hover:rgb(52, 59, 88);
  --Toggle: linear-gradient(to right, hsl(210, 78%, 56%) , hsl(146, 68%, 55%));
}

.light-theme {
  --BG: rgb(255, 255, 255);
  --BGPattern: hsl(225, 100%, 98%);
  --CardBG: hsl(227, 47%, 96%);
  --DText: hsl(228, 12%, 44%);
  --hover:rgb(231, 233, 235);
  --Text: hsl(230, 17%, 14%);
  --Toggle: hsl(230, 22%, 74%);
}

* {
  padding: 0;
  border: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  outline: none;
  font-family: "Inter", sans-serif;
}

body {
  background: var(--BG);
}

.main {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  width: 80%;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  gap: 2em;
}
.cards .card {
  width: 300px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: var(--CardBG);
  border-radius: 5px;
  gap: 10px;
  cursor: pointer;
}
.cards .card:hover {
  background: var(--hover);
  opacity: 1;
}
.cards .card p {
  color: var(--DText);
}
.cards .card .in {
  margin-top: 1em;
}
.cards .card .in h1 {
  font-size: 3em;
  color: var(--Text);
  font-weight: 700;
}
.cards .card .in p {
  letter-spacing: 2px;
  color: var(--DText);
  margin-top: 10px;
  font-size: 0.9em;
  text-transform: uppercase;
}
.cards .fcard {
  border-top-width: 4px;
  border-style: solid;
  border-color: hsl(208deg, 92%, 53%);
}
.cards .tcard {
  border-top-width: 4px;
  border-style: solid;
  border-color: hsl(203deg, 89%, 53%);
}
.cards .Icard {
  border-top-width: 4px;
  border-style: solid;
  border-image: linear-gradient(to right, hsl(37deg, 97%, 70%), hsl(329deg, 70%, 58%)) 1;
  border-radius: 10px;
}
.cards .Ycard {
  border-top-width: 4px;
  border-style: solid;
  border-color: hsl(348deg, 97%, 39%);
}

.up {
  color: hsl(163deg, 72%, 41%);
  padding: 0 0.3em;
  font-size: 14px;
  font-weight: 400;
  margin-top: 2.2em;
}

.down {
  color: hsl(356deg, 69%, 56%);
  padding: 0 0.3em;
  font-size: 14px;
  font-weight: 400;
  margin-top: 2.2em;
}

.main__section {
  width: 100%;
  margin-bottom: 1em;
}
.main__section .details .details__card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  gap: 2em;
}
.main__section .details .details__card .all {
  background: var(--CardBG);
  border-radius: 5px;
  padding: 30px;
  cursor: pointer;
}
.main__section .details .details__card .all:hover {
  background: var(--hover);
  opacity: 1;
}
.main__section .details .details__card .all .info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 50px;
}
.main__section .details .details__card .all .info img {
  object-fit: contain;
}
.main__section .details .details__card .all .info p {
  text-transform: capitalize;
  font-weight: 700;
  font-size: 1.2em;
  color: var(--DText);
}
.main__section .details .details__card .all .ret {
  display: flex;
  flex-direction: row;
  height: 0px;
  align-items: center;
  justify-content: space-between;
}
.main__section .details .details__card .all .ret h2 {
  color: var(--Text);
  font-size: 1.7em;
}
.main__section .details .details__card .all .ret p {
  position: relative;
  top: -15px;
}

.main__header {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  margin-top: 30px;
}
.main__header .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: var(--Text);
}
.main__header .top .right {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main__header .top .right p {
  font-weight: 700;
  color: var(--Text);
  font-size: 1.3em;
  text-transform: capitalize;
}
.main__header .top .right small {
  font-weight: 700;
  text-transform: capitalize;
  color: var(--DText);
}
.main__header .top .left {
  display: flex;
  flex-direction: row;
  gap: 10px;
  padding: 0.5em 0 5em 0;
  justify-items: center;
}
.main__header .top .left .dark {
  color: var(--Toggle);
  font-weight: 700;
  position: relative;
}
.main__header .top .left input {
  appearance: none;
  width: 50px;
  height: 20px;
  cursor: pointer;
  border-radius: 20px;
  background: var(--Toggle);
}
.main__header .top .left .dot {
  background: var(--CardBG);
  border-radius: 100%;
  height: 18px;
  left: -30px;
  top: 1px;
  position: relative;
  width: 18px;
  transform: translateX(0px);
  transition: transform 0.3s ease-out;
}
.main__header .top .left .dot.toggle {
  transform: translateX(-28px);
  transition: transform 0.3s ease-out;
}

.bg {
  position: absolute;
  top: 0;
  background-color: var(--BGPattern);
  z-index: -110;
  width: 100%;
  height: 250px;
}

p {
  display: flex;
  place-items: center;
  gap: 0.3em;
}

.ocer {
  margin-block: 1.2em;
  font-size: 1.5em;
  color: var(--Text);
}